nav{
    height: 100vh;
    width: 4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: rgb(27, 27, 27);
}
.img_shape{
    background-color: rgb(27, 27, 27);
    text-align: center;
    padding-bottom:1.2rem ;
    width: 4rem;
}
.div_img{
    background-color: rgb(27, 27, 27);
    width: 50%;
    color: #B47124;
}
.img_logo{
    background-color: rgb(27, 27, 27);
    width: 4rem;
    padding: 0.8rem;
}
.Link,.Navlink,.a{
    background-color: rgb(27, 27, 27);
}
.active path{
    width: 5rem;
    fill: #B47124;
}
@media only screen and (max-width: 700px){
    nav{
        width: 100vw;
        height: 5rem;
        flex-direction: column;
    }
    .link_cont{
        display: flex;
        background-color: rgb(27, 27, 27);
        /* align-items: center; */
        justify-content: space-around;
    }
    .div_img{
        background-color: rgb(27, 27, 27);
        width: 70%;
        color: #B47124;
    }
    .img_shape>div{
        display: inline-block;
    }
    .img_logo{
        display: none;
    }
    .a{
        display: none;
    }
}

